import React, { useState } from 'react';
import { Spin as Hamburger } from 'hamburger-react';

export default function Header() {
const [isOpen, setOpen] = useState(false)

const toggleMenu = () => {
    setOpen(!isOpen);
  };

  return (
    <div className='bg-[rgb(237,231,222)]'>
        <div className='h-24 max-w-[1240px] mx-auto flex items-center justify-between px-8'>
            <h1>My Portfolio</h1>
            <ul className='hidden md:flex'>
                <li className='p-4'>Home</li>
                <li className='p-4'>About</li>
                <li className='p-4'>Work</li>
                <li className='p-4'>Contact</li>
            </ul>
            <div className="md:hidden">
                <Hamburger toggled={isOpen} toggle={toggleMenu} size={20}/>
            </div>
        </div>
        <div>
            {isOpen && (
                <div className="flex flex-col items-center justify-center h-screen w-full text-[rgb(238,49,47)]">
                    <ul className="flex flex-col text-center">
                        <li className="p-4">Home</li>
                        <li className="p-4">About</li>
                        <li className="p-4">Projects</li>
                        <li className="p-4">Contact</li>
                    </ul>
                </div>
            )}
        </div>
    </div>
  )
}
